.buttonstyle{
	margin: 0 auto;
	width: 10em;
}

.wrapper{
    position: relative;
    clear:both;
    margin: 0 auto 75px auto;
    width: 100%;
    overflow: hidden;
  }
  .top{
    background: #2c9676;
    height: 180px;
    border-top: 20px solid #222;
  }
  
  .top .title {
    width: 700px;
    margin: 38px auto 0 auto; 
  }
  
  .title h1 {
    font-size:24px;
    color:#FFF;
    font-weight:500;
  }
  
  .content{
    padding-bottom: 20px;
  }
  
  .content .card.first {
    margin-top: -80px;
  }
  
  .card{
    position: relative;
    background: #fff;
    padding:50px;
    width: 600px;
    margin: 20px auto 0 auto;
    box-shadow: 0 2px 4px rgba(100,100,100,.1);
  }
  
  .card h2 {
    font-size:21px;
    font-weight:500;
  }
  
  .card h2 a {
    color:black;
    text-decoration:none;
  }
  
  .card .date {
    color:#9e9e9e;
    margin-top:10px;
    font-size:14px;
  }
  
  .card .text {
	color:#212121;
	text-align : left;
    margin-top:20px;
    font-size:15px;
    line-height:22px;
  }